import React from 'react'
import ReactMarkdown from 'markdown-to-jsx'
import { Typography, Link,  withStyles } from '@material-ui/core'

const styles = theme => ({
  listItem: {
    marginTop: theme.spacing(1)
  },
  blockquote: {
    marginLeft: theme.spacing(2),
    marginRight: theme.spacing(2),
    padding: theme.spacing(1),
    borderLeft: '5px solid',
    borderLeftColor: theme.palette.secondary.dark,
    //backgroundColor: theme.palette.info.light,
  },
  pre: {
  }
})

const options = {
  overrides: {
    h1: {
      component: Typography,
      props: {
        gutterBottom: true,
        variant: 'h4'
      }
    },
    h2: { component: Typography, props: { gutterBottom: true, variant: 'h6' } },
    h3: { component: Typography, props: { gutterBottom: true, variant: 'subtitle1' } },
    h4: {
      component: Typography,
      props: { gutterBottom: true, variant: 'caption', paragraph: true }
    },
    p: { component: Typography, props: { paragraph: true, variant: 'body2' } },
    a: { component: Link },
    pre: {
      component: withStyles(styles)(({ classes, ...props }) => (
        <pre className="gatsby-highlight">
          <Typography component="pre" variant="body2" {...props} />
        </pre>
      ))
    },
    li: {
      component: withStyles(styles)(({ classes, ...props }) => (
        <li className={classes.listItem}>
          <Typography component="span" variant="body2" {...props} />
        </li>
      ))
    },
    blockquote: {
      component: withStyles(styles)(({ classes, ...props }) => (
        <blockquote className={classes.blockquote}>
          <Typography component="span" variant="body2" {...props} />
        </blockquote>
      ))
    }
  }
}

/**
 * Component mapping html from markdown to Typography components in Material-ui
 * @param children
 * @param props
 * @returns {*}
 * @constructor
 */
export default function Markdown({ children, ...props }) {
  return (
    <ReactMarkdown options={options} {...props}>
      {children}
    </ReactMarkdown>
  )
}
